.play-conteiner {
  height: 100%;
  text-align: center;
  height: 650px;
  max-width: 750px;
  border: 3px solid;
  border-radius: 15px;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  align-items: center;
  padding: 60px 30px 30px;
  animation: 2s forwards aparecerPage;
  display: flex;
}

@keyframes aparecerPage {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@media (min-width: 650px) {
  .play-conteiner {
    width: 550px;
  }
}

@media (max-width: 362px) {
  .play-conteiner {
    padding: 60px 15px 15px;
  }
}

.buttons-cont {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  display: grid;
}

.op-piedra {
  grid-area: 2 / 1;
}

.op-tijera {
  grid-area: 2 / 3;
}

.op-papel {
  grid-area: 1 / 2;
}

.noPlay-container {
  height: 1px;
  width: 1px;
  height: 150px;
  width: 250px;
  background-color: #7fffd4;
  border: 2px solid;
  border-radius: 25% 10px;
  margin: auto;
  padding: 20px;
  display: none;
  position: absolute;
  inset: 0;
  box-shadow: 5px 5px 25px;
}

.desactivar {
  animation: 2s forwards desaparecer;
}

@keyframes desaparecer {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.active {
  flex-direction: column;
  align-items: flex-end;
  animation: 2s linear forwards aparecer;
  display: flex;
}

@keyframes aparecer {
  from {
    opacity: 0;
  }

  to {
    opacity: .8;
  }
}

.check {
  height: 60px;
  width: 60px;
  cursor: pointer;
  background-color: coral;
  border: solid;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.check:hover {
  height: 70px;
  width: 70px;
  border: 5px solid;
}

.expandir {
  animation: 1s alternate-reverse expandir;
}

@keyframes expandir {
  0% {
    scale: 1;
  }

  50% {
    scale: 1.5;
  }

  100% {
    scale: 1;
  }
}

.show-play {
  width: 300px;
  height: 650px;
  max-width: 550px;
  margin: auto;
  display: none;
  position: absolute;
  inset: 0;
}

@media (min-width: 650px) {
  .show-play {
    width: 300px;
  }
}

@media (min-width: 362px) {
  .show-play {
    width: 300px;
  }
}

.aparecer {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.cpu, .user {
  height: 300px;
  width: 300px;
  justify-content: center;
  align-items: center;
  position: absolute;
}

.cpu {
  opacity: 0;
  animation: 3s ease-in 1.5s forwards showCpu;
  top: -100px;
  transform: rotate(180deg);
}

.user {
  opacity: 0;
  z-index: 10;
  animation: 3s ease-in 1.5s forwards showUser;
  bottom: -10px;
}

@keyframes showUser {
  0% {
    opacity: 0;
    bottom: -10px;
  }

  50% {
  }

  100% {
    opacity: 1;
    bottom: 25px;
  }
}

@keyframes showCpu {
  from {
    opacity: 0;
    top: -100px;
  }

  to {
    opacity: 1;
    top: 0;
  }
}

